.doc-body {
  font-size: 1rem;
}

.doc-layout {
  display: flex;
  width: 1200px;
  margin: 0 auto;
}

.doc-content {
  max-width: 800px;
}

.doc-sidebar {
  width: 200px;
  flex: none;
  overflow-y: visible;
}

.doc-code-example {
  max-height: 500px;
  overflow-y: auto;
  overflow-x: hidden;
}

.display-2 {
  margin-bottom: 0.5rem;
}

.doc-content .btn-group {
  margin: 0.125rem 0;
}
.doc-content .form-control + .form-control {
  margin-top: 0.5rem;
}
.doc-content .progress + .progress {
  margin-top: 1rem;
}